.home-page{

.text-zone{
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 40%;
    max-height: 90%;
}

    h1 {
        color: #fff;
        font-size: 53px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;

        &::before{ // this adds and sets the <h1> tag, for a web design look
            content:'<h1>';
            font-family: 'La Belle Aurore';
            color:#f67280;
            font-size: 18px;
            position: absolute;
            margin-top: -4;
            left: 15px;
            opacity: 0.6;
        }

        &::after{
            content:'<h1/>';
            font-family: 'La Belle Aurore';
            color:#f67280;
            font-size: 18px;
            position: absolute;
            margin-top: 18px;
            margin-left: 20px;
            animation: fadeIn 1s 1.7s backwards;
            opacity: 0.6;

        }

        
        img{
            width: 45px; //adjust main page logo size
            margin-left: 15px;
            opacity: 0;
            height: auto;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;

        }
    }

    h2 {
        color:#6c5b7b;
        margin-top: 20px;
        font-weight: 500;
        font-size: 14px;
        font-family: sans-serif;
        letter-spacing: 3px;
        animation: fadeIn 1s 1.8s backwards;
    }

    .flat-button{
        color: #f67280;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 18px;
        border:  1px solid #f67280;
        margin-top: 25px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        white-space: nowrap;

        &:hover {
            background:#f67280;
            color:#fff
        }
    }

}